.card-wrap {
  padding: 1.5rem 0;
  grid-column: 5 / 15;
}

.card-flex {
  --cardGap: 1.5rem;
  --cardWidth: 20rem;
  display: flex;
  gap: var(--cardGap);
  padding-right: 1.5rem;
  width: calc(
    (var(--cardWidth) * var(--cardCount)) +
      (var(--cardGap) * (var(--cardCount) - 1))
  );
}

.card {
  flex: 0 0 var(--cardWidth);
  overflow: hidden;
  border-radius: 1rem;
  box-shadow: 0 0.25rem 1rem #0002;
  text-decoration: none;
  color: #000;
  background-color: #fff;
  transition: box-shadow 0.2s;
  .img-sizer {
    padding-top: 150%;
    position: relative;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  &:hover,
  &:focus {
    box-shadow: 0 0.25rem 2rem #0003;
  }
  &:last-child {
    margin-right: 1.5rem;
  }
  img {
    width: 100%;
    display: block;
  }
  // div {
  //   padding: 1.5rem;
  // }
  h3,
  p {
    margin: 0;
  }
}

.ball {
  width: 7.5rem;
  height: 7.5rem;
  background-color: #000;
  color: #d6869e;
  font-size: 1.25rem;
  font-weight: bold;
  // text-transform: uppercase;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  pointer-events: none;
  z-index: 100000;
}

