.fancy-text {
  font-size: 7.25rem;
  font-family: "Brygada 1918", serif;
  line-height: 1;
  background-color: #d6869e;
  color: #000;
  // background-image: radial-gradient(closest-side, #d0d029, #004869);
  // background-size: 10rem 10rem;
  // background-position: left bottom;
  background-image: radial-gradient(closest-side, $c-turq, #009ba600);
  background-size: 25rem 25rem;
  // background-position: 60% top, left bottom;
  background-position: var(--pos1);
  background-repeat: no-repeat;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
  -ms-text-fill-color: transparent;
  -o-text-fill-color: transparent;
  text-fill-color: transparent;
  // animation: fancyText 5s alternate infinite linear;
  &--wrap {
    grid-column: 4 / span 10;
    // max-width: 30rem;
  }
}

@keyframes fancyText {
  0% {
    // background-size: 15rem 15rem, 10rem 10rem;
    background-position: 60% top, left bottom;
  }
  33% {
    // background-size: 15rem 15rem, 10rem 10rem;
    background-position: 40% 60%, left 30%;
  }
  66% {
    // background-size: 15rem 15rem, 10rem 10rem;
    background-position: 20% 30%, 50% 60%;
  }
  100% {
    // background-size: 15rem 15rem, 10rem 10rem;
    background-position: -20% -20%, right 80%;
  }
}

.services {
  padding: 4rem 0;
  // .container {
  //   display: flex;
  //   justify-content: flex-end;
  // }
  &__balls {
    // width: 66.6666%;
    height: 40rem;
    position: relative;
    grid-column: 5 / 15;
    &::before {
      content: " ";
      position: absolute;
      border-radius: 1rem 0 0 1rem;
      top: 0;
      left: 0;
      width: 100vw;
      height: 100%;
      border: 2px solid $c-yellow;
      border-right: 0;
    }
  }
  &__ball {
    --size: 12.5rem;
    width: var(--size);
    height: var(--size);
    border-radius: 50%;
    background-color: $c-yellow;
    color: #000;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    font-size: 1.25rem;
    text-align: center;
    padding: 1rem;
    text-decoration: none;
    position: absolute;
    &.services__ball--mixer {
      background-color: #d0d02977;
    }
    &--turquoise {
      background-color: $c-turq;
      color: #fff;
      &.services__ball--mixer {
        background-color: #009ba677;
      }
    }
    &--pink {
      background-color: #ed6995;
      &.services__ball--mixer {
        background-color: #ed699577;
      }
    }
    &--blue {
      background-color: $c-blue;
      color: #fff;
      &.services__ball--mixer {
        background-color: #00486977;
      }
    }
    $time: 8s;
    &--track-1 {
      left: 3%;
      top: 3%;
      animation: ballOne 6s infinite linear;
    }
    &--track-2 {
      left: 35%;
      top: 20%;
      animation: ballTwo 8s infinite linear;
    }
    &--track-3 {
      left: 15%;
      bottom: 5%;
      animation: ballThree 7s infinite linear;
    }
    &--track-4 {
      left: 65%;
      bottom: 15%;
      animation: ballFour 9s infinite linear;
    }
    &--track-5 {
      left: 75%;
      top: 10%;
      animation: ballFive 8.5s infinite linear;
    }
  }
}

@keyframes ballOne {
  0% {
    left: 3%;
    top: 3%;
  }
  33.3333% {
    left: 12%;
    top: 8%;
  }
  66.6666% {
    left: 6%;
    top: 20%;
  }
  100% {
    left: 3%;
    top: 3%;
  }
}

@keyframes ballTwo {
  0% {
    left: 35%;
    top: 20%;
  }
  33.3333% {
    left: 25%;
    top: 15%;
  }
  66.6666% {
    left: 45%;
    top: 10%;
  }
  100% {
    left: 35%;
    top: 20%;
  }
}

@keyframes ballThree {
  0% {
    left: 15%;
    bottom: 5%;
  }
  33.3333% {
    left: 30%;
    bottom: 10%;
  }
  66.6666% {
    left: 5%;
    bottom: 25%;
  }
  100% {
    left: 15%;
    bottom: 5%;
  }
}

@keyframes ballFour {
  0% {
    left: 65%;
    bottom: 15%;
  }
  33.3333% {
    left: 40%;
    bottom: 5%;
  }
  66.6666% {
    left: 50%;
    bottom: 35%;
  }
  100% {
    left: 65%;
    bottom: 15%;
  }
}

@keyframes ballFive {
  0% {
    left: 75%;
    top: 10%;
  }
  33.3333% {
    left: 85%;
    top: 30%;
  }
  66.6666% {
    left: 60%;
    top: 20%;
  }
  100% {
    left: 75%;
    top: 10%;
  }
}
