@use "sass:math";

$wrapper: 90rem;

@mixin gridWinged($xs: 12, $s: 12, $m: 12, $l: 12, $xl: 12, $gap: false) {
  display: grid;
  @if ($gap) {
    gap: #{$gap}rem;
  }
  $gapSub: 0;
  @if ($gap) {
    $gapSub: $gap * ($xs + 1);
  }
  $colWidth: math.div(($wrapper - $gapSub), $xs);
  grid-template-columns:
    1fr repeat($xs, min(#{$colWidth}, calc((100% - #{$gapSub}rem) / #{$xs})))
    1fr;
}
